.post-item-wide {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -webkit-box-shadow: 0px 3px 2px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 3px 2px rgba(0, 0, 0, 0.2);
  overflow: hidden;
}

.post-item-background-image-small-wide {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.post-item-background-image-small-wide img {
  width: 40%;
  height: auto;
}

.post-item-info-wide {
  position: relative;
  -ms-flex: 1 1;
      flex: 1 1;
  background-color: #ffffff;
  padding: 15px;
  padding-left: 36px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-align: stretch;
      align-items: stretch;
  -ms-flex-pack: justify;
      justify-content: space-between;
  line-height: 24px;
}

.arrow-left {
  position: absolute;
  top: calc(50% - 16px);
  left: -16px;
  z-index: 5;
  width: 0;
  height: 0;
  border-top: 16px solid transparent;
  border-bottom: 16px solid transparent;

  border-right: 16px solid white;
}

.post-item-background-image-wide img {
  width: 100%;
  height: auto;
}



